import React from "react";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import CircularProgress from "@material-ui/core/CircularProgress";
import axios from "axios";
import { API_MAGAZINE } from "../../constant";
import { setSnackbar } from "../../controller/site";
import { useDispatch } from "react-redux";
import matchSorter from "match-sorter";

export default function MagazineAutocomplete(props) {
    const { input, label, initVal, isRequired, ...rest } = props; //  initVal
    const [open, setOpen] = React.useState(false);
    const [loading, setLoading] = React.useState(false);
    const [magazineList, setMagazineList] = React.useState([]);
    const [value, setValue] = React.useState(null); //  value 
    const dispatch = useDispatch();

    React.useEffect(() => {
        if (!loading && magazineList.length === 0) {
            setLoading(true);
            axios
                .get(API_MAGAZINE, {
                    withCredentials: true,
                    validateStatus: (status) => status === 200,
                })
                .then((res) => res.data)
                .then((res) => {
                    if (typeof res.magazines === "object") {
                        setMagazineList(res.magazines);
                        //  initVal 
                        if (initVal) {
                            const foundMagazine = res.magazines.find(
                                (magazine) => magazine.id === initVal
                            );
                            if (foundMagazine) {
                                setValue(foundMagazine); //  value
                            }
                        }
                    }
                })
                .catch((err) => {
                    dispatch(setSnackbar("", "error"));
                })
                .finally(() => setLoading(false));
        }
    }, [open]);

    return (
        <Autocomplete
            open={open}
            onOpen={() => setOpen(true)}
            onClose={() => setOpen(false)}
            loading={loading}
            loadingText="..."
            noOptionsText=""
            options={magazineList}
            value={value} //  value
            onChange={(event, newValue) => {
                setValue(newValue); //  value
                input.onChange(newValue ? { id: newValue.id, name: newValue.name } : null); //  onChange
            }}
            getOptionLabel={(option) => option.name}
            renderOption={(option) => option.name + "@" + option.id}
            getOptionSelected={(option, value) => option.id === value.id}
            filterOptions={(options, { inputValue }) =>
                matchSorter(options, inputValue, { keys: ["name", "id"] })
            }
            renderInput={(params) => (
                <TextField
                    required={isRequired}
                    {...params}
                    label={label}
                    InputProps={{
                        ...params.InputProps,
                        endAdornment: (
                            <React.Fragment>
                                {loading ? <CircularProgress size={20} /> : null}
                                {params.InputProps.endAdornment}
                            </React.Fragment>
                        ),
                    }}
                />
            )}
            {...rest} // 
        />
    );
}