import React, { useState, memo, useMemo, useCallback, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, TextField, Popover } from '@material-ui/core';
import { debounce } from 'lodash';
import Box from "@material-ui/core/Box";

const useStyles = makeStyles((theme) => ({
  calendarContainer: {
    maxWidth: '350px',
    minWidth: '280px',
    margin: '0 auto',
    padding: '16px',
    border: '1px solid #ccc',
    borderRadius: '8px',
  },
  weekDayHeader: {
    display: 'grid',
    gridTemplateColumns: (props) => (props.mode === 0 ? 'repeat(7, 1fr)' : '50px repeat(7, 1fr)'),
    gap: '4px',
    marginBottom: '8px',
    fontWeight: 'bold',
  },
  dayContainer: {
    display: 'grid',
    gridTemplateColumns: (props) => (props.mode === 0 ? 'repeat(7, 1fr)' : '50px repeat(7, 1fr)'),
    gap: '4px',
  },
  weekNumber: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    fontWeight: 'bold',
    minWidth: '50px',
  },
  dayButton: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: '50%',
    cursor: 'pointer',
    border: '1px solid #ccc',
    aspectRatio: '1 / 1',
    '&:hover': {
      backgroundColor: '#f0f0f0',
    },
  },
  selectedDay: {
    backgroundColor: '#3f51b5',
    color: '#fff',
    border: 'none',
  },
  emptyDay: {
    visibility: 'hidden',
  },
}));
const CustomDatePicker = memo(({ mode = 2, value = [], onChange }) => {
  const classes = useStyles({ mode });

  // 
  const weekDays = useMemo(() => ['', '', '', '', '', '', ''], []);

  // 135
  const days = useMemo(() => Array.from({ length: 35 }, (_, i) => i + 1), []);

  // 11
  const startDayOfWeek = 1; // 0 = Sunday, 1 = Monday, ..., 6 = Saturday

  // 1
  const emptyDays = useMemo(
    () => Array(startDayOfWeek - 1).fill(null).map((_, i) => i + 1),
    [startDayOfWeek]
  );

  // 
  const weeks = useMemo(() => {
    const weeks = [];
    let currentWeek = [];
    let weekNumber = 1;

    // 
    emptyDays.forEach(() => {
      currentWeek.push(null);
    });

    // 
    days.forEach((day) => {
      currentWeek.push(day);
      if (currentWeek.length === 7) {
        weeks.push({ weekNumber, days: currentWeek });
        currentWeek = [];
        weekNumber++;
      }
    });

    // 7
    if (currentWeek.length > 0) {
      while (currentWeek.length < 7) {
        currentWeek.push(null);
      }
      weeks.push({ weekNumber, days: currentWeek });
    }

    return weeks;
  }, [days, emptyDays]);

  // 
  const handleDayClick = useCallback(
    (day) => {
      const newSelectedDays = new Set(value); //  value  selectedDays
      if (newSelectedDays.has(day)) {
        newSelectedDays.delete(day); // 
      } else {
        newSelectedDays.add(day); // 
      }
      onChange([...newSelectedDays]); // 
    },
    [value, onChange]
  );

  return (
    <div className={classes.calendarContainer}>
      <Typography variant="h6" align="center" gutterBottom>
        
      </Typography>

      {/*  */}
      {mode !== 0 && (
        <div className={classes.weekDayHeader}>
          {mode !== 0 && <div></div>}
          {weekDays.map((day) => (
            <Typography key={day} variant="body2" align="center">
              {day}
            </Typography>
          ))}
        </div>
      )}

      {/*  */}
      <div className={classes.dayContainer}>
        {weeks.map((week) => (
          <React.Fragment key={week.weekNumber}>
            {/*  */}
            {mode !== 0 && (
              <div className={classes.weekNumber}>{week.weekNumber}</div>
            )}

            {/*  */}
            {week.days.map((day, index) => {
              //  mode  0  32-35 
              if (mode === 0 && day && day >= 32) {
                return null;
              }

              return day ? (
                <div
                  key={day}
                  className={`${classes.dayButton} ${
                    value.includes(day) ? classes.selectedDay : ''
                  }`}
                  onClick={() => handleDayClick(day)}
                >
                  {mode === 1 ? '' : day}
                </div>
              ) : (
                <div key={`empty-${index}`} className={`${classes.dayButton} ${classes.emptyDay}`} />
              );
            })}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
});

const MultiDatePicker = memo(({ mode = 2, value = [], onChange }) => {
  const [anchorEl, setAnchorEl] = useState(null);

  const handleOpenPicker = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClosePicker = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);

  //  value 
  const safeValue = Array.isArray(value) ? value : [];

  return (
    <Box display="flex" alignItems="flex-end" flexGrow={1}>
      <TextField
        value={safeValue.join(', ')} //  safeValue
        onClick={handleOpenPicker}
        placeholder=""
        fullWidth
        InputProps={{ readOnly: true }}
      />
      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={handleClosePicker}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left',
        }}
        disableAutoFocus //  Popover 
        disableEnforceFocus //  Popover 
        disableRestoreFocus //  Popover 
      >
        <CustomDatePicker
          mode={mode}
          value={safeValue} //  safeValue
          onChange={onChange}
        />
      </Popover>
    </Box>
  );
});

export default MultiDatePicker;