import React from "react";
import MaterialTable from "material-table";
import {useHistory} from "react-router";
import {tableIcons} from "../Component/MaterialTable/tableIcons";
import {useSelector} from "react-redux";

export default function UsersTable(props) {
    const history = useHistory();
    const privilege = useSelector(state => state.user.privilege);
    const {users} = props;

    const columns = [
        {title: "", field: "nickname"},
        {title: "ID", field: "uid"},
    ];
    if (privilege>2) { columns.push({ title: "", field: "permissions" , lookup: {
        0: "",
        1: "",
        2: "",
    },}); }

    return (
        <MaterialTable icons={tableIcons} columns={columns} options={{
            pageSize: 20
        }} data={users} components={{
            Toolbar: props => (<></>)
        }} onRowClick={(event, rowData, togglePanel) => {
            history.push("/user/" + rowData["uid"]);
        }} localization={{
            grouping: {
                placeholder: "...",
                groupedBy: ":"
            },
            pagination: {
                labelDisplayedRows: '{from}{to}  {count}',
                labelRowsSelect: '',
                labelRowsPerPage: ':',
                firstAriaLabel: '',
                firstTooltip: '',
                previousAriaLabel: '',
                previousTooltip: '',
                nextAriaLabel: '',
                nextTooltip: '',
                lastAriaLabel: '',
                lastTooltip: ''
            },
        }}/>
        );
}